import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button,Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import  Header  from "../Header";
import  Footer  from "../Footer";
import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
class Profile extends Component {
    
    state = {userId:'',item:[],error:'',isLoading:false,loadingColor:'#ffffff',pageNo:0,mobileNo:'',firstName:'',lastName:'',userPassword:'',email:'',licenseLink:'',idNumber:''};
    constructor(props) {
        super(props);    
        //console.log("++pickup**********==="+JSON.stringify(this.props));    
      }
    
      componentDidMount() {    
          this.setState({isLoading:true});      
       let userId=localStorage.getItem("userId");
       this.setState({userId:userId});
              
        //this.setState({item:this.props.match.params.data});
        this.getProfile(userId);
      }
    
    async getProfile(userId){
        console.log("*****getBookigs******");   
        this.setState({error:''});
        //const headers = { 'Content-Type': 'application/json' } 
        let token=localStorage.getItem("token");
        let pageNo=this.state.pageNo+1;
        
        const headers = {'Authorization':`Bearer ${token}`} ;
          let urlData="&userId="+userId+"&pageId="+pageNo;
          //const response = await fetch('http://localhost:3001/booking/getCabs?originObj='+originObj+'&destinationObj='+destinationObj, { headers });
          
          const response = await fetch(process.env.REACT_APP_APIURL+'user/get_user_byid?'+urlData, { headers });
          //console.log("+++response=="+JSON.stringify(response))
          const dataRes = await response.json();
                    
          if(dataRes.code==200){
            console.log("idProof:"+dataRes.data.idProof);
                let idProof=dataRes.data.idProof;
              this.setState({firstName:dataRes.data.firstName});
              this.setState({lastName:dataRes.data.lastName});
              this.setState({mobileNo:dataRes.data.mobileNo});
              this.setState({email:dataRes.data.email});
              this.setState({licenseLink:dataRes.data.idProof});
              this.setState({idNumber:dataRes.data.idNumber});
              this.setState({item:dataRes.data});
          }else{              console.log("errorr")
              this.setState({error:'some internal error please try later'})
          }
          
          this.setState({isLoading:false});
          //this.setState({cabsList:data.data});
    }
    
    loadScript=(src) =>{
        return new Promise((resolve) => {
            const script = document.createElement("script");
            script.src = src;
            script.onload = () => {
                resolve(true);
            };
            script.onerror = () => {
                resolve(false);
            };
            document.body.appendChild(script);
        });
    }
    

    updateProfile = async () => {
        try {
            this.setState({error:''});
            let token = localStorage.getItem("token");

            const headers = { 'Authorization': `Bearer ${token}` };
            
            if (this.state.userPassword == "" || this.state.userPassword == null) {
                this.setState({ error: "Please enter password" })
                return false;
            }
            const data = {
                userId: this.state.userId,
                userPassword: this.state.userPassword,
            };


            const result = await axios.post(process.env.REACT_APP_APIURL + "user/update_password", data, { headers });
            console.log("result:" + JSON.stringify(result));
            if (result.data.code == 200) {
                this.setState({ error: result.data.message });
                this.setState({userPassword:''})
                //this.getDrivers(this.state.userId);
            } else {

                this.setState({ error: result.data.message })
            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
        this.setState({ isLoading: false });
    }
    setUserPassword = (userPassword) => {
        this.setState({ userPassword: userPassword.target.value });
    }
    render() { 
        const override =`
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (
           
            <div> 
                <Header/> 
                <div>
                <ClipLoader css={override} style={{borderColor:'red'}} color={this.state.loadingColor} loading={this.state.isLoading}  size={150} >  </ClipLoader>
                </div>
                
                
                <div> 
                <section id="pricing" className="pricing">

                    <div className="container" data-aos="fade-up" style={{width:'95%!important'}}>
                        
                        <div className="row">                       
                            <div className="col-lg-12 col-md-12">
                                    
                                    <Card>
                                        <Card.Title style={{fontSize:16,padding:10,color:'white',backgroundColor:'gray'}}>Profile</Card.Title>
                                        <Card.Body>
                                            <div style={{color:'red'}}>{this.state.error}</div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>First Name</Form.Label>
                                                        <Form.Control type="text" disabled={true} placeholder="First Name" value={this.state.firstName} />                                                                                                        
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail">
                                                        <Form.Label>Last Name</Form.Label>
                                                        <Form.Control type="text" disabled={true} placeholder="Last Name" value={this.state.lastName} />                                                                                                        
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                    <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Mobile No.</Form.Label>
                                                        <Form.Control type="text" disabled={true} placeholder="Mobile No" value={this.state.mobileNo} />                                                                                                        
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Email</Form.Label>
                                                        <Form.Control type="email" disabled={true} placeholder="Mobile No" value={this.state.email} />                                                                                                        
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Diver License</Form.Label>
                                                        <Form.Control type="text" disabled={true} placeholder="License No" value={this.state.idNumber} />                                                                                                        
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                <Form.Group controlId="formBasicEmail" >
                                                        <img src={this.state.licenseLink} width={100} />
                                                    </Form.Group>
                                                    
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                            <Form.Group controlId="formBasicEmail" >
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control name="userPassword" value={this.state.userPassword} type="text" placeholder="Password" onChange={this.setUserPassword} />
                                </Form.Group>
                                            </div>
                                            <div className="col-12">        
                                                
                                                <Form.Group controlId="formBasicEmail" style={{float:'right'}}>
                                                    <Button variant="primary" type="button" onClick={this.updateProfile.bind(this)}>
                                                        Update password
                                                    </Button>                                                                                                       
                                                </Form.Group>
                                            </div>
                                            
                                        </Card.Body>
                                    </Card>                                   
                            </div>             
                            
                        </div>

                    </div>
                    
                </section>
                 
                    
                </div>
            </div>
        );
    }
}
 
export default withRouter(Profile);